@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;600;700&family=Bebas+Neue&family=Georama:wght@400;500;700&family=DM+Serif+Display:ital@0;1&family=Fira+Code:wght@400;500;600&display=swap');

.pixel-header {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  position: relative;
  contain: layout style paint;
  scroll-snap-align: start;
}

.hero-text {
  font-size: clamp(2.5rem, 8vw, 5.5rem);
  margin: 0;
  color: var(--text-color);
  transition: color var(--theme-transition);
  
  /* Keep the hero text rigid */
  display: flex;
  align-items: baseline; 
  justify-content: center;
  height: 1.5em; 
  line-height: 1;
  /* Adjustable width for the 'Jake' text */
  --jake-slot-width: 3.00ch;
  position: relative;
  z-index: 1;
}

.hero-prefix {
  display: inline-block;
  white-space: pre;
}
/* Stars layer that fades in on dark mode and toward the horizon */
.stars-layer {
  position: absolute;
  inset: 0;
  pointer-events: none;
  z-index: 0;
  opacity: 0;
  transition: opacity var(--theme-transition);
  mask-image: linear-gradient(to bottom,
    rgba(0,0,0,1) 0%,
    rgba(0,0,0,0.5) 70%,
    rgba(0,0,0,0) 100%
  );
}

[data-theme='dark'] .stars-layer { opacity: 1; }

.star {
  position: absolute;
  transform: translate(-50%, -50%);
}

/* Keep star color constant; only fade opacity on theme change */
[data-theme='dark'] .star { color: #ffffff; }

/* Special formatting for the 'Jake' text */
.highlight-slot {
  display: inline-block;
  width: var(--jake-slot-width);
  height: 1em;
  text-align: center;
  vertical-align: baseline;
  overflow: visible;
}

.hero-text .highlight {
  color: var(--dot-color);
  display: inline-block;
  white-space: nowrap;
  height: 1em;
  line-height: 1;
  padding: 0;
  margin-left: var(--jake-offset);
  font-weight: 400;
  font-style: normal;
  overflow: visible;
  transition: font-family 0.05s ease;
}

/* No text select on the hero text */
.hero-text,
.hero-text * {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* The fonts for the rapid swap */
.font-bbh-bartle { font-family: 'BBH Bartle', 'Bebas Neue', sans-serif; }
.font-playfair { font-family: 'Playfair Display', serif; }
.font-bebas { font-family: 'Bebas Neue', sans-serif; }
.font-geom { font-family: 'Georama', sans-serif; }
.font-dm-serif { font-family: 'DM Serif Display', serif; }
.font-fira-code { font-family: 'Fira Code', monospace; }
